//=================================================
// Icons
//=================================================

.fas {
	&.fas-space-l {
		padding-left: 0.526rem;
	}

	&.fas-space-r {
		padding-right: 0.526rem;
	}
}

.search-form {
	.search {
		.fas {
			&.fa-search {
				font-size: 0.947rem;
			}
		}
	}
}

.menu-toggle {
	.fas {
		&.fa-bars {
			height: 0.684rem;
			overflow: hidden;
		}
	}
}

/*=============================
// Star raiting
=============================*/
.star-raiting {
	@extend .d-flex;
	@extend .no-space;
	color: #f8bc1c;

	[class*='fa-'] {
		margin-left: 0.263rem;
		font-size: 0.608rem;
		@extend .align-self-center;

		&:first-child {
			margin-left: 0;
		}
	}
}

/*=============================
// Scroll
=============================*/
.scroll-animation {
	position: relative;
	z-index: 10;

	> * {
		position: relative;
		-moz-animation: adv-bounce 1s infinite linear;
		-o-animation: adv-bounce 1s infinite linear;
		-webkit-animation: adv-bounce 1s infinite linear;
		animation: adv-bounce 1s infinite linear;
		@extend .transition-all;
	}
}

@keyframes adv-bounce {
	0% {
		top: 0;
	}
	50% {
		top: -30px;
	}
	70% {
		top: -15px;
	}
	100% {
		top: 0;
	}
}

/*=============================
// Close
=============================*/
.close {
	opacity: 1;
	@extend .transition-all;

	> span {
		&:not(.adveits-i) {
			@include lhCrop-b(1, 0.55);
			@include lhCrop-a(1, 0.55);
		}
	}

	&:hover {
		color: $body_links_hover_color;
		@extend .transition-all;

		.adveits-i {
			&.adv-close {
				span {
					background-color: $body_links_hover_color;
				}
			}
		}
	}

	&:not(:disabled):not(.disabled) {
		&:hover, &:focus {
			opacity: 1;
		}
	}
}

